/**
 * Sets the current RTL value.
 */
export function setRTL(isRTL) {
  if (_rtl !== isRTL) {
    _rtl = isRTL;
  }
}
/**
 * Gets the current RTL value.
 */

export function getRTL() {
  if (_rtl === undefined) {
    _rtl = typeof document !== 'undefined' && !!document.documentElement && document.documentElement.getAttribute('dir') === 'rtl';
  }

  return _rtl;
}

var _rtl = getRTL();

export function getStyleOptions() {
  return {
    rtl: getRTL()
  };
}